<template>
  <div class="position-relative h-100 w-100">
    <div class="map-header position-absolute text-light">
      <h2 class="h3 m-0"><span>Day 07 - </span>Green</h2>
    </div>
    <div class="position-absolute search-wrapper">
      <div class="input-group shadow">
        <label for="search" class="form-label visually-hidden"
          >Filter windfarms:</label
        >
        <input
          class="form-control"
          type="text"
          name="search"
          autocomplete="off"
          placeholder="Search windfarms"
          v-model="searchTerm"
        />
        <button type="button" class="btn btn-secondary" @click="resetSearch()">
          <b-icon-search v-if="!searchTerm"></b-icon-search>
          <b-icon-x-lg v-if="searchTerm"></b-icon-x-lg>
        </button>
      </div>
      <ul class="list-unstyled bg-secondary" v-if="showResultsList">
        <li v-for="(feature, index) in filteredWindfarms.features" :key="index">
          <button
            type="button"
            class="btn btn-dark rounded-0 w-100 text-left"
            @click="navigateTo(feature)"
          >
            {{ feature.properties["Wind farm"] }},
            {{ feature.properties.County }}, {{ feature.properties.Country }}
          </button>
        </li>
      </ul>
    </div>
    <dl class="map-source position-absolute d-flex">
      <dt class="me-1">Source:</dt>
      <dd class="text-break">
        <a
          href="https://en.wikipedia.org/wiki/List_of_onshore_wind_farms_in_the_United_Kingdom"
          target="_blank"
          >List of onshore wind farms in the United Kingdom - Wikipedia</a
        >
      </dd>
    </dl>
    <div ref="map-root" class="basemap"></div>
  </div>
</template>

<script>
import mapboxgl from "mapbox-gl";
import "mapbox-gl/src/css/mapbox-gl.css";
import windfarms from "../assets/on-shore-windfarms.geojson";
import AnimatedPopup from "mapbox-gl-animated-popup";

export default {
  data() {
    return {
      accessToken:
        "pk.eyJ1Ijoia21hcnNoYWxsIiwiYSI6ImNrdXdudGlpODI2ejkydXF2bXcyM3poaWQifQ.hyBCr6uwWlDGMGyVmuKCIw",
      map: null,
      popup: null,
      searchTerm: null,
      windfarms: windfarms,
      showResultsList: false,
    };
  },
  watch: {
    searchTerm: function () {
      if (this.searchTerm && this.filteredWindfarms.features.length > 0) {
        this.setClusterSource(this.filteredWindfarms);
        this.showResultsList = true;
      } else {
        this.setClusterSource(this.windfarms);
        this.showResultsList = false;
      }
    },
  },
  computed: {
    filteredWindfarms: function () {
      let filtered = {
        type: "FeatureCollection",
        features: [],
      };
      filtered.features = this.windfarms.features.filter((feature) => {
        return (
          feature.properties["Wind farm"]
            .toLowerCase()
            .match(this.searchTerm.toLowerCase()) ||
          feature.properties.Country.toLowerCase().match(
            this.searchTerm.toLowerCase()
          ) ||
          feature.properties.County.toLowerCase().match(
            this.searchTerm.toLowerCase()
          ) ||
          feature.properties.Model.toLowerCase().match(
            this.searchTerm.toLowerCase()
          ) ||
          feature.properties.Developer.toLowerCase().match(
            this.searchTerm.toLowerCase()
          ) ||
          feature.properties.Operator.toLowerCase().match(
            this.searchTerm.toLowerCase()
          ) ||
          feature.properties.Owner.toLowerCase().match(
            this.searchTerm.toLowerCase()
          )
        );
      });
      return filtered;
    },
  },
  methods: {
    setBounds(collection) {
      var bounds = new mapboxgl.LngLatBounds();
      collection.features.forEach(function (feature) {
        bounds.extend(feature.geometry.coordinates);
      });
      this.map.fitBounds(bounds);
    },
    clearPopups() {
      if (this.popup !== null) {
        this.popup.remove();
      }
    },
    resetSearch() {
      this.searchTerm = null;
      this.setClusterSource(this.windfarms);
    },
    setClusterSource(geoJSON) {
      this.map.getSource("windfarms").setData(geoJSON);
      this.setBounds(geoJSON);
    },
    navigateTo(result) {
      if (result.geometry.coordinates) {
        this.map.flyTo({
          center: result.geometry.coordinates,
          zoom: 9,
          essential: true, // this animation is considered essential with respect to prefers-reduced-motion
        });
      } else {
        this.map.fitBounds(result.bbox, { zoom: 16 });
      }
    },
    initMap() {
      mapboxgl.accessToken = this.accessToken;

      this.map = new mapboxgl.Map({
        container: this.$refs["map-root"],
        style: "mapbox://styles/kmarshall/ckvnvu4hz2btz14p87s6ob4tu",
        center: [-4.306061, 54.4790791],
        zoom: 5,
      });

      this.map.on("load", () => {
        this.map.addSource("windfarms", {
          type: "geojson",
          data: this.windfarms,
          cluster: true,
          clusterMaxZoom: 14,
          clusterRadius: 50,
        });

        this.map.addLayer({
          id: "clusters",
          type: "circle",
          source: "windfarms",
          filter: ["has", "point_count"],
          paint: {
            "circle-color": [
              "step",
              ["get", "point_count"],
              "#6ab045",
              10,
              "#67cc31",
              20,
              "#5afc03",
            ],
            "circle-radius": [
              "step",
              ["get", "point_count"],
              20,
              10,
              30,
              20,
              40,
            ],
          },
        });

        this.map.addLayer({
          id: "cluster-count",
          type: "symbol",
          source: "windfarms",
          filter: ["has", "point_count"],
          layout: {
            "text-field": "{point_count_abbreviated}",
            "text-font": ["DIN Offc Pro Medium", "Arial Unicode MS Bold"],
            "text-size": 12,
          },
        });

        this.map.addLayer({
          id: "unclustered-point",
          type: "circle",
          source: "windfarms",
          filter: ["!", ["has", "point_count"]],
          paint: {
            "circle-color": "#ffffff",
            "circle-radius": 10,
          },
        });

        // inspect a cluster on click
        this.map.on("click", "clusters", (e) => {
          const features = this.map.queryRenderedFeatures(e.point, {
            layers: ["clusters"],
          });
          const clusterId = features[0].properties.cluster_id;
          this.map
            .getSource("windfarms")
            .getClusterExpansionZoom(clusterId, (err, zoom) => {
              if (err) return;

              this.map.easeTo({
                center: features[0].geometry.coordinates,
                zoom: zoom,
              });
            });
        });

        this.map.on("mouseenter", "unclustered-point", (e) => {
          this.map.getCanvas().style.cursor = "pointer";
          const coordinates = e.features[0].geometry.coordinates.slice();
          const properties = e.features[0].properties;

          let tableRowMarkup =
            '<table class="table table-sm table-striped mb-0">';
          for (let [key, value] of Object.entries(properties)) {
            if (value) {
              tableRowMarkup += "<tr>";
              tableRowMarkup += '<th scope="row">' + key + ": </th>";
              tableRowMarkup += "<td>" + value + "</td>";
              tableRowMarkup += "</tr>";
            }
          }
          tableRowMarkup += "</table>";

          // Ensure that if the map is zoomed out such that
          // multiple copies of the feature are visible, the
          // popup appears over the copy being pointed to.
          while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
            coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
          }

          this.popup = new AnimatedPopup({
            offset: 5,
            openingAnimation: {
              duration: 100,
              easing: "linear",
            },
            closingAnimation: {
              duration: 100,
              easing: "linear",
            },
            closeButton: false,
          })
            .setLngLat(coordinates)
            .setHTML(tableRowMarkup)
            .addTo(this.map);
        });

        this.map.on("mouseleave", "unclustered-point", () => {
          this.clearPopups();
          this.map.getCanvas().style.cursor = "default";
        });
        // disable map rotation using right click + drag
        this.map.dragRotate.disable();

        // disable map rotation using touch rotation gesture
        this.map.touchZoomRotate.disableRotation();

        this.map.on("mouseenter", "clusters", () => {
          this.map.getCanvas().style.cursor = "pointer";
        });
        this.map.on("mouseleave", "clusters", () => {
          this.map.getCanvas().style.cursor = "default";
        });
      });

      const nav = new mapboxgl.NavigationControl();
      this.map.addControl(nav, "top-right");
      this.map.scrollZoom.disable();
    },
  },
  mounted() {
    this.initMap();
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/map-shared.scss";

:deep(.mapboxgl-popup-content) {
  color: #191919;
  z-index: 3;
  font-size: 0.8rem;
  font-family: inherit;
  width: 300px;
  max-width: unset;
}

.search-wrapper {
  z-index: 2;
  margin-top: 62px;
  padding: 1rem;
  width: 350px;
}
.list-unstyled {
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: auto;

  button {
    text-align: left;
    transition: background 250ms ease;
    font-size: 0.8rem;
    &:hover {
      background: rgba(255, 255, 255, 0.05);
    }
  }
}
</style>
